@use "../global" as *;

/* about
***************************************************************/
.p-about {
  padding-block: 6.4rem 0;
  position: relative;
  margin-block-end: -0.5rem;//画像の影分の調整

  @include mq(sp) {
    padding-block: 3.2rem 1.2rem;
    margin-block-end: 0;
  }
}

.p-about__text {
  @include font(17, 400, 27.2, 0);
  color: $cl-text;
  margin-block-start: 1.7rem;

  @include mq(sp) {
    font-size: 1.4rem;
    margin-block-start: 1.2rem;
  }
}

.p-about__cta {
  translate: 0 -0.7rem;
}

.p-about__img {
  max-width: 128rem;
  padding-inline: 1.7rem 0.5rem;
  margin-inline:auto;
  margin-top: 1.4rem;

  @include mq(sp) {
    padding-inline: 1.2rem 0.5rem;
    margin-top: 0.8rem;
  }
}
