@use "../global" as *;

/* 導入事例
***************************************************************/

.p-case-study {
  padding-block: 6.4rem;

  @include mq(sp) {
    padding-block: 4rem;
  }
}

.p-case-study__container {
  margin-block-start: 4rem;
  display: flex;
  flex-direction: column;
  gap: 4rem;

  @include mq(sp) {
    margin-block-start: 3.2rem;
    gap: 3rem;
  }
}

.p-case-study__item {
  display: flex;
  gap: 4rem;
  align-items: center;
  justify-content: flex-start;

  @include mq(sp) {
    flex-direction: column;
    gap:1.6rem;
    padding-bottom: 5.5rem;

    &:not(:last-child) {
      border-bottom: 1px solid #DDD9D7;
    }
  }
}

.p-case-study__item-img {
  border-radius: 3rem;
  overflow: hidden;
  width: calc(572 / 1184 * 100%);
  flex-shrink: 0;

  img {
    object-fit: cover;
  }

  &.img01 img{
    aspect-ratio: 572 / 353;
  }
  &.img02 img{
    aspect-ratio: 572 / 345;
  }

  @include mq(sp) {
    width: 100%;

    &.img01 img{
      aspect-ratio: 335 / 200;
    }
    &.img02 img{
      aspect-ratio: 335 / 193;
    }
  }
}

.p-case-study__item-body {
  width: calc(532 / 1184 * 100%);
  display: flex;
  flex-direction: column;
  gap: 4rem;

  @include mq(sp) {
    width: 100%;
    gap: 2.4rem;
  }
}

.p-case-study__item-title {
  @include font(17, 700, 22.1, 0);
  color: $cl-text;
}

.p-case-study__item-logo {
  width: 24rem;
  img {
    object-fit: contain;
  }
}

.p-case-study__item-name {
  margin-block-start: 2rem;
  @include font(17, 400, 27.2, 0);
  color: $cl-text;

  @include mq(sp) {
    margin-block-start: 0.8rem;
    font-size: 1.4rem;
  }
}

.p-case-study__item-detail {
  margin-block-start: 0.8rem;
  @include font(17, 400, 27.2, 0);
  color: $cl-text;

  @include mq(sp) {
    margin-block-start: 0.8rem;
    font-size: 1.4rem;
  }
}
