@use "../global" as *;

/* 料金プラン
***************************************************************/
.p-plan {
  padding-block: 6.4rem;
  background: $cl-bg;

  @include mq(sp) {
    padding-block: 3.2rem;
  }
}

.p-plan__container {
  max-width: 101rem;
  margin-inline:auto;
  margin-block-start: 4.4rem;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 2.4rem;

  @include mq(sp) {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    margin-block-start: 4.2rem;
  }
}

.p-plan__card {
  border-radius: 2rem;
  padding: calc(2rem - 0.2rem) calc(3rem - 0.2rem);
  border: 0.2rem solid#0098BB;
  color: $cl-black-mild;
  background: $cl-white;

  &:nth-child(2) {
    background: #E9FBFF;
  }

  &:nth-child(3) {
    background: #CBECF2;
  }
}

.p-plan__card-class {
  @include font(17, 400, 27.2, 0);
  text-align: center;
  span {
    color: $cl-blue;
  }
}

.p-plan__card-price {
  text-align: center;
  margin-block-start: 1.6rem;

  @include mq(sp) {
    margin-block-start: 0.8rem;
  }
}

.p-plan__card-price-num {
  @include font(56, 700, 56, 0);
  font-family: $ff-poppins;

  @include mq(sp) {
    font-size: 4.8rem;
  }
}

.p-plan__card-price-unit {
  @include font(20, 700, 26, 0);
}

.p-plan__card-price-tax {
  @include font(12, 400, 16.8, 0);
  font-family: $ff-roboto;
}
