@use "../global" as *;

/* よくある課題
***************************************************************/

.p-problem {
  padding-block: 6.4rem;
  background: $cl-bg;

  @include mq(sp) {
    padding-block: 3.2rem;
  }
}

.p-problem__container {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 4rem;
  margin-block-start: 4rem;

  @include mq(sp) {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 2.4rem;
    margin-block-start: 2.4rem;
  }
}

.p-problem__card {
  @include mq(sp) {
    display: flex;
    gap: 1.6rem;
    align-items: flex-start;
  }
}

.p-problem__card-img {
  overflow: hidden;
  border-radius: 3rem;
  img {
    aspect-ratio: 266 / 200;
    object-fit: cover;
  }

  @include mq(sp) {
    border-radius: 2rem;
    width: 13rem;
    flex-shrink: 0;
    img {
      aspect-ratio: 1;
    }
  }
}

.p-problem__card-body {
  margin-block-start: 2.4rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;

  @include mq(sp) {
    margin-block-start: 0;
    gap: 1.2rem;
  }
}

.p-problem__card-title {
  @include font(17, 700, 22.1, 0);
  color: $cl-black-mild;
}

.p-problem__card-text {
  @include font(17, 400, 27.2, 0);
  color: $cl-text;

  @include mq(sp) {
    font-size: 1.4rem;
  }
}
