@use "../global" as *;

/* 利用フロー
***************************************************************/

.p-flow {
  padding-block: 6.4rem;

  @include mq(sp) {
    padding-block: 3.2rem;
  }
}

.p-flow__container {
  display: flex;
  gap: 8rem;
  margin-block-start: 4.75rem;
  align-items: flex-start;

  @include mq(sp) {
    flex-direction: column;
    margin-block-start: 3.2rem;
    gap: 3.2rem;
  }
}

.p-flow__img {
  width: calc(414 / 1184 * 100%);
  img {
    aspect-ratio: 414 / 537;
  }

  @include mq(sp) {
    width: 100%;
    img {
      aspect-ratio: 335 / 420;
    }
  }
}

.p-flow__contents {
  width: calc(603 / 1184 * 100%);
  margin-block-start: 4.55rem;

  @include mq(sp) {
    width: 100%;
    margin-block-start: 0;
  }
}

.p-flow__step {
  padding: 1.9rem 4rem 2rem 0;
  display: flex;
  gap: 3rem;
  border-top: 1px solid #DDD9D7;

  @include mq(sp) {
    padding: 1.1rem 0 1.2rem 0;
    gap: 1.6rem;

    &:not(:first-child) {
        padding: 1.5rem 0 1.6rem 0;
    }
  }
}

.p-flow__step-num {
  width: 5.5rem;
  flex-shrink: 0;
}

.p-flow__step-num-icon {
  width: 5.5rem;
  img {
    aspect-ratio: 111 / 112;
  }
}

.p-flow__step-num-text {
  text-align: right;
  @include font(14, 700, 20, 0);
  color: $cl-light-blue;

  span {
    font-family: Roboto;
  }
}

.p-flow__step-body {
  flex: 1;
}

.p-flow__step-title {
  @include font(17, 700, 22.1, 0);
  color: $cl-black-mild;
}

.p-flow__step-text {
  margin-block-start: 0.8rem;
  @include font(17, 400, 26.86, 0);
  color: $cl-text;

  @include mq(sp) {
    font-size: 1.4rem;
  }
}
