@use "../global" as *;

/* fv
***************************************************************/
.p-fv {
  position: relative;
  padding-block: 5rem 7.8rem;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: min(calc(611 / 1280 * 100vw), 73.3rem);
    height: min(calc(479.5 / 1280 * 100vw) , 55.5rem);
    background: url(../img/img_fv--pc.webp) no-repeat center bottom / cover;
  }

  @include mq(sp) {
    padding-block: 1.2rem 35rem;

    &::before {
      top: auto;
      bottom: 1.2rem;
      right: 0;
      width: 36.9rem;
      height: 38.95rem;
      background: url(../img/img_fv--sp.webp) no-repeat center center / cover;
    }
  }
}

.p-fv__container {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;

  @include mq(sp) {
    gap: 1.6rem;
  }
}

.p-fv__title {
  @include font(42, 700, 67.2, -2.1);
  color: $cl-black-mild;

  @include mq(sp) {
    @include font(30, 700, 45, -0.9);
  }
}

.p-fv__text {
  @include font(17, 400, 27.2, 0);
  color: $cl-text;
  min-height: 8.1rem;
}

.p-fv__cta {
  display: flex;
  gap: 2.4rem;

  @include mq(sp) {
    gap: 1.2rem;
  }
}

.p-fv__notes {
  @include font(12, 400, 16.8, -0.12);
  color: #736B67;
}
