@use "../global" as *;

/* 利用シーン・ユースケース
***************************************************************/
.p-use-case {
  padding-block: 6.4rem;

  @include mq(sp) {
    padding-block: 3.2rem;
  }
}

.p-use-case__container {
  margin-block-start: 4rem;
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 4rem;

  @include mq(sp) {
    margin-block-start: 3.2rem;
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}

.p-use-case__item {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}

.p-use-case__item-img {
  overflow: hidden;
  border-radius: 3rem;
  img {
    aspect-ratio: 266 / 200;
    object-fit: cover;
  }

  @include mq(sp) {
    img {
      aspect-ratio: 335 / 200;
      object-fit: cover;
    }
  }
}

.p-use-case__item-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 1.6rem;
  &:not(:first-child) {
    margin-block-start: 1.6rem;
  }

  >li {
    @include font(17, 700, 22.1, 0);
    color: $cl-black-mild;
    display: flex;
    gap: 0.8rem;
    align-items: center;

    &:nth-child(odd) {
      min-width: 13.9rem;
    }

    &::before {
      content: '';
      background: url(../img/icon_check-box.webp) no-repeat center center / contain;
      width: 2.9rem;
      height: 2.5rem;
    }
  }

  @include mq(sp) {
    >li {
      &:nth-child(odd) {
        min-width: 16.1rem;
      }
    }
  }
}
