//===========================================
//   フォント(400,700)
//===========================================

$ff-base: "Noto Sans JP", sans-serif;
$ff-dm: "DM Sans", sans-serif;
$ff-roboto: "Roboto", sans-serif;
$ff-poppins: "Poppins", sans-serif;


//===========================================
//   レイアウト (単位不要)
//===========================================
$pc-inner: 1184;  // pc版inner幅
$pc-sidePadding: 48; //pc版サイドのpadding
$pc-inner-sum: calc($pc-inner + $pc-sidePadding * 2);
$breakpoint: 768; // ブレイクポイント (pc版カンプとsp版カンプの境界)
$sp-sidePadding: 20; //sp版サイドのpadding
$pc-camp: 1280; // pc版のカンプサイズを入力
$sp-camp: 375; // sp版のカンプサイズを入力
$sp-inner: 600;// 768px以下のinner幅を入力
$sp-inner-sum: calc($sp-inner + $sp-sidePadding * 2);

$sm-breakpoint: 500; // small版ブレイクポイント

//headerの高さ
$pc-header-height: 94;
$sp-header-height: 80;

//z-index
$z-header: 100;
$z-drawerIcon: 102;
$z-drawer: 101;


//===========================================
//   カラー変数
//===========================================

$cl-black:#000000;
$cl-black-mild:#191817;
$cl-text:#413C3A;
$cl-white:#ffffff;
$cl-pink:#EA526F;
$cl-blue:#0098BB;
$cl-light-blue:#00AED4;
$cl-bg:#F7F6F4;