@use "../global" as *;

/* ルートフォント設定(PCファースト)
***************************************************************/
html {
  font-size: 62.5%;

  @media screen and (max-width: #{px($pc-inner-sum)}) and (min-width: #{px($breakpoint)}) {
		font-size: calc(10 / #{$pc-inner-sum} * 100vw);
	}

  @media screen and (max-width: #{px($breakpoint) - 1px}) {
    font-size: min(calc(10 / #{$sp-camp} * 100vw) , 14px);
	}

  @include mq(sp) {
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar{
        display: none;
    }
  }
}


/* スクロール設定
***************************************************************/
html {
  scroll-padding-top:rem($pc-header-height);

	@include mq(sp) {
		scroll-padding-top:rem($sp-header-height);
	}
}

/* base（基本）設定
***************************************************************/
body {
  font-family: $ff-base;
}

a {
  text-decoration: none;
  transition: 0.3s;
  color: inherit;

  @media (any-hover: hover) {
    &:hover {
      cursor: pointer;
    }
  }
}

picture,
img,
a,
span {
  display: inline-block;
}

picture,
video,
img,
svg {
  width: 100%;
  height: 100%;
}


/* PC時は電話番号リンクを無効化
***************************************************************/
@media (min-width: 768px) {
  a[href*="tel:"] {
      pointer-events: none;
      cursor: default;
      text-decoration: none;
  }
}


