@use "../global" as *;

/* 共通ボタン
***************************************************************/
//メインボタン（ピンク）
.c-button {
  height: 4.8rem;
  border-radius: 100vmax;
  @include font(14, 700, 17, 0);
  color: $cl-white;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  justify-content: flex-end;
  width: 16.2rem;
  padding-inline: calc(1.6rem - 0.2rem * 2);
  background: $cl-pink;
  border: 0.2rem solid transparent;

  &::after {
    content: '';
    width: 2.4rem;
    height: 2.4rem;
    background: url(../img/icon_button-arrow--white.webp) no-repeat center center / contain;
  }

  @media (any-hover: hover) {
    &:hover {
      opacity: 0.7;
    }
  }
}

//headerボタン（ピンク）
.c-button__header {
  height: 4.8rem;
  border-radius: 100vmax;
  @include font(14, 700, 17, 0);
  white-space: nowrap;
  color: $cl-white;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 13.8rem;
  padding-inline: calc(1.6rem - 0.2rem * 2);
  background: $cl-pink;
  border: 0.2rem solid transparent;

  @media (any-hover: hover) {
    &:hover {
      opacity: 0.7;
    }
  }
}

//headerボタン（ブルー枠）
.c-button__header--frame {
  height: 4.8rem;
  border-radius: 100vmax;
  @include font(14, 700, 17, 0);
  white-space: nowrap;
  color: $cl-blue;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16.6rem;
  padding-inline: calc(1.6rem - 0.2rem * 2);
  background: $cl-white;
  border: 0.2rem solid $cl-blue;

  @media (any-hover: hover) {
    &:hover {
      background: $cl-blue;
      color: $cl-white;
    }
  }
}


//FVボタン（ピンク）
.c-button__fv {
  height: 4.8rem;
  border-radius: 100vmax;
  @include font(14, 700, 17, 0);
  white-space: nowrap;
  color: $cl-white;
  display: flex;
  align-items: center;
  gap: 1.8rem;
  justify-content: flex-end;
  width: 19rem;
  padding-inline: 1.6rem;
  background: $cl-pink;
  border: 0.2rem solid transparent;

  &::after {
    content: '';
    width: 2.4rem;
    height: 2.4rem;
    background: url(../img/icon_button-arrow--white.webp) no-repeat center center / contain;
  }

  @media (any-hover: hover) {
    &:hover {
      opacity: 0.7;
    }
  }

  @include mq(sp) {
    width: 16.15rem;
    height: 5.6rem;
    justify-content: center;

    &::after {
      display: none;
    }
  }
}

//FVボタン（ブルー枠）
.c-button__fv--frame {
  height: 4.8rem;
  border-radius: 100vmax;
  @include font(14, 700, 17, 0);
  white-space: nowrap;
  color: $cl-blue;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  justify-content: flex-end;
  width: 19rem;
  padding-inline: 1.6rem;
  background: $cl-white;
  border: 0.2rem solid $cl-blue;

  &::after {
    content: '';
    width: 2.4rem;
    height: 2.4rem;
    background: url(../img/icon_button-arrow--blue.webp) no-repeat center center / contain;
    transition: all  0.3s ease;
  }

  @media (any-hover: hover) {
    &:hover {
      background: $cl-blue;
      color: $cl-white;

      &::after {
        background: url(../img/icon_button-arrow--white.webp) no-repeat center center / contain;
      }
    }
  }

  @include mq(sp) {
    width: 16.15rem;
    height: 5.6rem;
    justify-content: center;

    &::after {
      display: none;
    }
  }
}


//Largeボタン（ピンク）
.c-button__large {
  height: 4.8rem;
  border-radius: 100vmax;
  @include font(14, 700, 17, 0.42);
  white-space: nowrap;
  color: $cl-white;
  display: flex;
  align-items: center;
  gap: 3.95rem;
  justify-content: flex-end;
  width: 27.5rem;
  padding-inline: 1.6rem;
  background: $cl-pink;
  border: 0.2rem solid transparent;

  &::after {
    content: '';
    width: 2.4rem;
    height: 2.4rem;
    background: url(../img/icon_button-arrow--white.webp) no-repeat center center / contain;
  }

  @media (any-hover: hover) {
    &:hover {
      opacity: 0.7;
    }
  }
}


//Largeボタン（ブルー枠）
.c-button__large--frame {
  height: 4.8rem;
  border-radius: 100vmax;
  @include font(14, 700, 17, -0.42);
  white-space: nowrap;
  color: $cl-blue;
  display: flex;
  align-items: center;
  gap: 3.95rem;
  justify-content: flex-end;
  width: 27.5rem;
  padding-inline: calc(1.6rem - 0.2rem * 2);
  background: $cl-white;
  border: 0.2rem solid $cl-blue;

  &::after {
    content: '';
    width: 2.4rem;
    height: 2.4rem;
    background: url(../img/icon_button-arrow--blue.webp) no-repeat center center / contain;
  }

  @media (any-hover: hover) {
    &:hover {
      background: $cl-blue;
      color: $cl-white;

      &::after {
        background: url(../img/icon_button-arrow--white.webp) no-repeat center center / contain;
      }
    }
  }
}