@use "../global" as *;

/* アニメーション
***************************************************************/

/*下からふわっ（強め） */
.fade-in-up--strong{
  opacity: 0;
  transform: translateY(50px);
	transition: opacity 1s, transform 1s;

  &.js-show {
    opacity: 1;
    transform: translateY(0);
  }
}

/*下からふわっ */
.fade-in-up{
  opacity: 0;
  transform: translateY(30px);
	transition: opacity 1s, transform 1s;

  &.js-show {
    opacity: 1;
    transform: translateY(0);
  }
}