@use "../global" as *;

/* 共通inner
***************************************************************/

.l-inner {
  position: relative;
  width: 100%;
  max-width: px($pc-inner-sum);
  height: inherit;
  padding: 0 rem($pc-sidePadding);
  margin-inline: auto;

  @include mq(sp) {
    width: 100%;
    max-width: px($sp-inner-sum);
    padding: 0 rem($sp-sidePadding);
  }
}

