@use "../global" as *;

/* ヘッダー
***************************************************************/

.l-header {
	width: 100%;
	position: absolute;
	top:0;
	left:0;
	z-index: 100;
	background-color: $cl-white;
}

.l-header__inner {
	display: grid;
	grid-template-columns: 19.8rem 1fr;
	gap: 5.2rem;
	padding-inline: 4.8rem;
	padding-block: 2rem 2.2rem;
	max-width: 128rem;
	margin-inline:auto;
	align-items: center;
	height: rem($pc-header-height);

	@include mq(sp) {
		grid-template-columns: 12.2rem 1fr;
		padding-inline: 2rem;
		padding-block: 1.6rem 3.2rem;
		height: rem($sp-header-height);
	}
}

.l-header__logo {
	width: 19.8rem;

	@include mq(sp) {
		width: 12.2rem;
	}
}

.l-header__nav {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 2.9rem;

	@include mq(sp) {
		display: none;
	}
}

.l-header__nav-list {
	display: flex;
	align-items: center;
	gap: 2.4rem;

	a {
		@include font(13, 700, 20.8, 0);
		white-space: nowrap;

		@media (any-hover: hover) {
			&:hover {
				opacity: 0.7;
			}
		}
	}
}

/* ヘッダー用CTA
***************************************************************/
.l-header__nav-cta {
	display: flex;
	align-items: center;
	gap: 1.6rem;
}