@use "../global" as *;

/* メリット
***************************************************************/
.p-merit {
  padding-block: 6.4rem;
  background: rgba(186, 231, 243, 0.40);

  @include mq(sp) {
    padding-block: 3.2rem;
  }
}

.p-merit__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 4.8rem 4rem;
  margin-block-start: 4rem;

  @include mq(sp) {
    margin-block-start: 3.2rem;
    gap: 3.1rem;
  }
}

.p-merit__card {
  padding: 2.4rem 3rem;
  width: 31rem;
  min-height: 30rem;
  background: $cl-white;
  border-radius: 3rem;
  box-shadow: 0.4rem 0.4rem 2rem rgba(0, 0, 0, 0.10);
  overflow: hidden;

  @include mq(sp) {
    width: 100%;
    min-height: auto;
  }
}

.p-merit__card-img {
  width: 8rem;
  margin-inline:auto;

  img {
    aspect-ratio: 1;
    object-fit: contain;
  }
}

.p-merit__card-title {
  @include font(17, 700, 22.1, 0);
  color: $cl-black-mild;
  margin-block-start: 2.4rem;

  &.p-merit__card-title--nowrap {
    white-space: nowrap;
  }

  @include mq(sp) {
    text-align: center;
  }
}

.p-merit__card-text {
  @include font(17, 400, 27.2, 0);
  color: rgba(0, 0, 0, 0.55);
  margin-block-start: 1.6rem;

  @include mq(sp) {
    font-size: 1.4rem;
  }
}
