@use "../global" as *;

/* セクション共通見出し
***************************************************************/

.c-section-head {
	display: flex;
	justify-content: space-between;
	gap: 4rem;
	align-items: flex-end;
}

.c-section-head--plan {
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 1.2rem;

	@include mq(sp) {
		align-items: flex-start;
	}
}

.c-section-head__title-wrap {
	display: flex;
	flex-direction: column;
	gap: 1.2rem;

	.c-section-head--plan & {
		align-items: center;

		@include mq(sp) {
			align-items: flex-start;
		}
	}
}

.c-section-head__sub {
	@include font(14, 700, 19.6, 0);
	color: $cl-blue;
	font-family: $ff-dm;
	width: fit-content;
	position: relative;
	text-transform: uppercase;

	&::after {
		content: '';
		position: absolute;
		left: calc(100% + 1rem);
		top: 50%;
		translate: 0 -50%;
		width: 5rem;
		height: 1px;
		background: $cl-blue;
	}

	.c-section-head--plan & {
		&::after {
			display: none;
		}

		@include mq(sp) {
			&::after {
				display: block;
			}
		}
	}
}

.c-section-head__title {
	@include font(35, 700, 45.5, 0);

	@include mq(sp) {
		font-size: 2.9rem;
	}
}

.c-section-head__cta {
	@include mq(sp) {
		display: none;
	}

	.c-section-head--plan & {
		display: block;
		margin-inline:auto;
	}
}

.c-section-head__text {
	@include font(17, 400, 27.2, 0);
	color: $cl-text;
	text-align: center;

	@include mq(sp) {
		font-size: 1.4rem;
		text-align: left;
	}
}