@use "../global" as *;

/* FAQ
***************************************************************/
.p-faq {
	padding-block: 6.4rem;

  @include mq(sp) {
    padding-block: 3.2rem;
  }
}

.p-faq__container {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  margin-block-start: 4rem;
}

.p-faq__item {
  padding: calc(3.2rem - 0.1rem);
  border: 1px solid #DDD9D7;
  border-radius: 1.6rem;
  overflow: hidden;
  background: white;
  box-shadow: 0 0.1rem 0.3rem rgba(221, 217, 214, 0.64);
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}

.p-faq__item-question {
  @include font(17, 700, 22.1, 0);
  color: $cl-black-mild;
  display: flex;
  align-items: center;
  gap: 1.6rem;

  &::before {
    content: '';
    background: url(../img/icon_faq.webp) no-repeat center center / contain;
    width: 4rem;
    height: 3.6rem;
    flex-shrink: 0;
  }

  @include mq(sp) {
    font-size: 2rem;
  }
}

.p-faq__item-answer {
  @include font(17, 400, 27.2, 0);
  color: $cl-black-mild;

  @include mq(sp) {
    font-size: 1.4rem;
  }
}
