@use "./variables" as *;

// =================================
//  メディアクエリ
// =================================

$breakpoints: (
    //キー : 値
    'sp': "screen and (max-width: #{$breakpoint - 1px})",
    'pc': "(min-width: #{$breakpoint * 1px})",
    'xl': "(min-width: 1440px)",
) !default;

@mixin mq($breakpoint: pc) {
    @media #{map-get($breakpoints, $breakpoint)} {
        @content;
    }
}

// =================================
//  フォント情報の一括入力
// =================================
//MAX75%のフォントサイズ以下にならない
@mixin font75($size: null, $weight: null, $height: null, $spacing: null) {
  @if $size {
		font-size: max(calc($size / 10 * 1rem), calc($size * 0.75px));
		@include mq(sp) {
			font-size: calc($size / 10 * 1rem);
		}
  }
  @if $weight {
    font-weight: $weight;
  }
  @if $height {
    line-height: calc($height / $size);
  }
  @if $spacing {
    letter-spacing: calc($spacing / $size * 1em);
  }
}

//完全に可変するフォントサイズ
@mixin font($size: null, $weight: null, $height: null, $spacing: null) {
  @if $size {
		font-size: calc($size / 10 * 1rem);
		@include mq(sp) {
			font-size: calc($size / 10 * 1rem);
		}
  }
  @if $weight {
    font-weight: $weight;
  }
  @if $height {
    line-height: calc($height / $size);
  }
  @if $spacing {
    letter-spacing: calc($spacing / $size * 1em);
  }
}